import React, { useEffect, useState } from "react"
import { Button, Container, makeStyles, Typography } from "@material-ui/core"
import { Pagination, Stack } from "@mui/material"
import { Box } from "@mui/system"
import { useDispatch, useSelector } from "react-redux"
import { useHistory } from "react-router"
import { getReviews } from "../../redux/reducers/reviews"
import Review from "./Review"

const Reviews = ({ t }) => {
	const classes = useStyles()
	const history = useHistory()
	const dispatch = useDispatch()

	const { reviews } = useSelector(({ reviewsReducer }) => reviewsReducer)

	const [page, setPage] = useState(1)
	const [count, setCount] = useState(0)

	useEffect(() => {
		dispatch(getReviews())
		// eslint-disable-next-line
	}, [])

	useEffect(() => {
		setCount(Math.ceil(reviews?.reviews?.length / 20))
	}, [reviews])

	const changeHandler = (event, value) => {
		setPage(value)
	}

	return (
		<section className={classes.section}>
			<Container maxWidth="lg">
				<Box className={classes.box}>
					<Typography variant="h2" className={classes.title}>
						{t("Reviews")}
					</Typography>
					<Button variant="contained" size="large" color="secondary" onClick={() => history.push("/dashboard/reviews/leave")}>
						{t("leaveReview")}
					</Button>
				</Box>
				{reviews?.reviews?.map((item) => (
					<Review item={item} key={item.id} />
				))}
				{count > 10 && (
					<Stack
						spacing={2}
						mt={"50px"}
						sx={{
							"& nav": {
								display: "flex",
								justifyContent: "center",
							},
						}}
					>
						<Pagination count={count} size="large" page={page} onChange={changeHandler} />
					</Stack>
				)}
			</Container>
		</section>
	)
}

export default Reviews

const useStyles = makeStyles((theme) => ({
	section: {
		padding: "80px 0 119px",
		background: "#F7F8F9",
	},

	title: {
		color: "#000",
	},

	box: {
		display: "flex",
		justifyContent: "space-between",
		marginBottom: 37,

		[theme.breakpoints.down(992)]: {
			flexDirection: "column",
			justifyContent: "center",
			alignItems: "center",

			"& button": {
				width: "100%",
				marginTop: 24,
			},
		},

		[theme.breakpoints.down(768)]: {
			"& button": {
				marginTop: 16,
			},
		},
	},
}))

